import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux'

import App from './App';
import store from './flux/store'

const ReactBody = () => {
  return (
    <Provider store={store}>
      <App />
    </Provider>
  )
}
// hydrate
ReactDOM.render(<ReactBody />, document.getElementById('react-body'));
