import React from 'react';
import axios from 'axios'
import { Base64 } from 'js-base64'
// import { connect } from 'react-redux'

import config from './utils/config.js'

import "./App.scss"
import Chat from './components/Chat'

import { setAuthToken, setCurrentUser } from './utils/setAuth'
if (typeof(window) === 'object') {
  var jwtToken;
  if (window.__INITIAL_TOKEN__) {
    try {
      var split = window.__INITIAL_TOKEN__.split('.')
      var payload = JSON.parse(Base64.decode(split[1]))
      if (payload.exp * 1000 > Date.now()) {
        jwtToken = window.__INITIAL_TOKEN__  // window 上的jwtToken
      }else {
        // 验证本地 token
        jwtToken = window.localStorage.jwtToken;
      }
    }catch(err) {
      console.error('tokenValidError', err.message)
      jwtToken = window.localStorage.jwtToken;
    }
  }

  if (jwtToken) {
    axios.defaults.headers.common["Authorization"] = jwtToken; // 设置请求头 token
    // 验证请求头token
    axios.get(`${config.HOST}/api/users/current`)
      .then(res => {
        if (!res.data.success) {
          setAuthToken(false);
          setCurrentUser({});
        } else {
          // token 验证成功
          setAuthToken(res.data.payload.token);
          setCurrentUser(res.data.payload.user);
        }
      })
      .catch(err => {
        setAuthToken(false);
        setCurrentUser({});
      });
  }else {
    setAuthToken(false);
    setCurrentUser({});
  }
}

const App = (props) => {
  return <Chat />
}


export default App
